.pop-down {
  position: absolute;
  border-width: 3px;
  border-style: solid;
  border-radius: 0.9rem;
  border-color: white;
  color: white;
  background-color: transparent;
  top: 5rem;
  left: 0;
  right: 0;
  margin: 0 auto;
  padding: 0.8rem 1.6rem;
  max-width: 28ch;
  font-size: 1.125rem;
  font-weight: 500;
  text-align: center;
  transform: translateY(-350%);
}

@keyframes popDown {
  0% {
    transform: translateY(-350%);
  }
  35% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(0);
  }
  60% {
    transform: translateY(0);
  }
  70% {
    transform: translateY(0);
  }
  80% {
    transform: translateY(0);
  }
  90% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(-350%);
  }
}
